import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { A1Component } from './a1/a1.component';
import { HeroDetailComponent } from './hero-detail/hero-detail.component';
import { HeroListComponent } from './hero-list/hero-list.component';
import { MatDatePickerComponent } from './matDatePicker/matDatePicker.component';
import { MatFormFieldComponent } from './matFormField/matFormField.component';
import { MatInputDemoComponent } from './matInputDemo/matInputDemo.component';
import { MatRadioButtonDemoComponent } from './matRadioButtonDemo/matRadioButtonDemo.component';
import { MatSliderDemoComponent } from './matSliderDemo/matSliderDemo.component';
import { MenuOverviewExampleComponent } from './MenuOverviewExample/MenuOverviewExample.component';

const routes: Routes = [
  {path:'a1',component:A1Component},
  {path:'dp',component:MatDatePickerComponent},
  {path:'mid',component:MatInputDemoComponent},
  {path:'menu',component:MenuOverviewExampleComponent},
  {path:'heroes',component:HeroListComponent,data:{animation:'heroes'}},
  {path:'hero/:id',component:HeroDetailComponent,data:{animation:'hero'}},
  {path:'',component:MatSliderDemoComponent},

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
